import { endpoints } from '../../consts/endpoints'
import { useQuery } from 'react-query'
import { useApiConnector } from '../useApiConnector'

export interface IUseGetAvatars {
	response: any | null
	isLoading: boolean
	isFetching: boolean
	error: string
}

export const USE_GET_ALL_AVATARS_QUERY_KEY = 'USE_GET_ALL_AVATARS_QUERY_KEY'

export const useGetAllAvatars = (): IUseGetAvatars => {
	const apiConnector = useApiConnector()
	const {
		data = [],
		isLoading,
		isFetching,
		isError,
		error,
	} = useQuery<any, Error>([USE_GET_ALL_AVATARS_QUERY_KEY], () => apiConnector(endpoints.allAvatars))

	return {
		response: data,
		isLoading,
		isFetching,
		error: isError ? error?.message ?? 'Ошибка' : '',
	}
}
